
import Scroll from '../../../base/scroll/scroll'
import Checkbtn from '../../../base/checkbtn/checkbtn'
import MButton from '../../../base/button/button'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapGetters } from 'vuex'
export default {
name: 'getUserMoney',
components: {
Scroll,
Checkbtn,
XInput,
Group,
MButton
},
computed: {
getMoneyInfo: {
get () {
return `${this.userInfo.bankCardUserName} ${this.userInfo.bankCardNo}`
},
set (val) {
return val
}
},
btnSaveState: {
get () {

if (this.money >= 200 && this.money <= this.availableDeposit && this.msg && this.msg.length === 6) {
return true
}
return false
},
set (val) {
return val
}
},
availableDeposit: {
get () {
let deposit = 400
if (this.userCoin.availableDeposit) {
deposit = parseFloat(this.userCoin.availableDeposit).toFixed(2)
}
return deposit
},
set (val) {
return val
}
},
userPhone () {
return this.userInfo.telephone
},
...mapGetters([
'userInfo',
'userCoin'
])
},
data () {
return {
btnCodeState: true,
msg: null,
pwd: null,
codeText: '',
time: 60,
timer: null,
chooseCheck: 1,
money: null
}
},
methods: {

getCode () {
if (this.btnCodeState) {
this.btnCodeState = false
this.$axios.post('/api/sms/send', {
telephone: this.userInfo.telephone,
type: 0
}).then((response) => {
if (response.data.code === '200') {
this.$vux.toast.text('', 'middle')
this.timer = setInterval(this.timeGo, 1000)
} else {
this.btnCodeState = true
this.$vux.alert.show({
title: '',
content: response.data.message
})
}
}).catch(() => {
this.btnCodeState = true
this.$vux.alert.show({
title: '',
content: ''
})
})
}
},

timeGo () {
if (this.time >= 0) {
this.codeText = `(${this.time}`
this.time--
} else {
this.codeText = ''
this.time = 60
clearInterval(this.timer)
this.timer = null
this.btnCodeState = true
}
},
getUserMoney () {
var _this = this
if (!this.btnSaveState) {
return false
}
this.checkCode().then((res) => {
if (res.data.code === '200') {
this.$vux.confirm.show({
title: '',
content: `<p>:${parseFloat(_this.money).toFixed(2)}</p><p>:${parseFloat(_this.money * 0.9).toFixed(2)}</p>`,
onConfirm () {
_this.doDeposit()
}
})
} else {
this.$vux.alert.show({
title: '',
content: res.data.message
})
}
}).catch(() => {
this.$vux.alert.show({
title: '',
content: ''
})
})
},

checkCode () {
return new Promise((resolve, reject) => {
this.$axios.post('/api/sms/verify', {
telephone: this.userInfo.telephone,
type: 0,
code: this.msg
}).then((response) => {
resolve(response)
}).catch((error) => {
reject(error)
})
})
},
doDeposit () {
this.$axios.post('/api/withdrawApply/buyer/insertWithdrawApply', {
withdrawType: 1,
buyerUserId: this.userInfo.buyerUserId,
deposit: this.money,
telephone: this.userInfo.telephone,
bankNo: this.userInfo.bankCardNo,
bankName: this.userInfo.bankName,
fullName: this.userInfo.fullName
}).then((response) => {
const _this = this
if (response.data.code === '200') {
this.$vux.alert.show({
title: '',
content: ',',
onHide () {
_this.$router.push({ name: 'user' })
}
})
} else {
this.$vux.alert.show({
title: '',
content: response.data.message
})
}
}).catch(() => {
this.$vux.alert.show({
title: '',
content: ''
})
})
}
}
}



